import React from "react";

import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import "bootstrap/dist/css/bootstrap.min.css";
import { registerAction } from "../../Action/userAction";
import "./SignUp.css";

const SignUp = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  const dispatch = useDispatch();
  const registerState = useSelector((state) => state.registerReducer);
  const { success, loading, error } = registerState;

  const handleLogin = (e) => {
    e.preventDefault();
    if (password !== confirmPassword) {
      alert("password does not matches");
    } else {
      const signUp = {
        name: name,
        email: email,
        password: password,
        confirmPassword: confirmPassword,
      };
      console.log(signUp);
      dispatch(registerAction(signUp));
    }
  };
  return (
    <div className="container-fluid">
      <div className="row min-vh-100 justify-content-center align-items-center">
        <div className="col-md-4">
          <div className="card">
            <div className="card-body">
              {success && (
                <div class="alert alert-success" role="alert">
                  User Registered Successfully
                </div>
              )}
              {error && (
                <div class="alert alert-success" role="alert">
                  User already exist
                </div>
              )}
              <h5 className="card-title text-center mb-4">Sign Up</h5>
              <form onSubmit={handleLogin}>
                <div className="mb-3">
                  <label htmlFor="name" className="form-label">
                    Name
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    id="name"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    required
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="username" className="form-label">
                    Email
                  </label>
                  <input
                    type="email"
                    className="form-control"
                    id="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    required
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="password" className="form-label">
                    Password
                  </label>
                  <input
                    type="password"
                    className="form-control"
                    id="password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required
                  />
                </div>
                <div className="mb-3">
                  <label htmlFor="password" className="form-label">
                    Confirm Password
                  </label>
                  <input
                    type="password"
                    className="form-control"
                    id="confirm-password"
                    value={confirmPassword}
                    onChange={(e) => setConfirmPassword(e.target.value)}
                    required
                  />
                </div>
                <div className="text-center">
                  <button type="submit" className="btn btn-primary">
                    Sign Up
                  </button>
                </div>
              </form>
              <a href="/"> back to home</a>
              <p>
                Already have an account?
                <a href="/login" id="login-here">
                  Log In
                </a>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default SignUp;
